@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
}

body {
  background-color: #ebfaff;
}

.App {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 30px;
  margin: 20px auto;
  max-width: 340px;
}
.App h1 {
  letter-spacing: 3px;
  font-size: 22px;
  font-weight: 500;
  border-radius: 10px;
  /* 추가 */
  background: linear-gradient(45deg, #35e1e1, #05a6bc);
  /* 추가 */
  color: #fff;
  text-align: center;
  padding: 20px;
}
.App ul {
  margin-top: 20px;
}
.App ul li {
  text-align: center;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px dashed #ddd;
  cursor: pointer;
}
.App ul li:hover {
  font-weight: bold;
  background-color: #ddd;
}